import React, { Component } from 'react';
import "../hub/Hub.css";
import Button from 'react-bootstrap/Button';
import { Link } from 'react-router-dom';

const baseapi = 'http://' + window.location.hostname + process.env.REACT_APP_PORT;
const url = baseapi + "/showsensors";

export default class Sensors extends Component {
  constructor() {
    super();
    this.state = {
      name: '',
      sensor: '',
      sensors: []
    }
  }

  componentDidMount() {
    fetch(url)
    .then(response => {
      return response.json();
    })
    .then(result => {
      console.log("result", result);
      this.setState({sensors: result});
      console.log("this.state.sensors", this.state.sensors);
    })
  }

  render() {
    let items = this.state.sensors;

    return (
      <div className="zones-page">
          <h1>Sensorer</h1>
          <div>
            {items.map(item => {
                let tis = item.tempis.toFixed(1);
                let divided = tis.toString();
                divided = divided.replace(".", ",");
                return <span key={item.id}>
                <Link to={`/sensor/${ item.id }`} className="btn btn-block"><Button variant="light" size="lg" block><span className="three"><span className="tempis"><b>{divided} &deg;C</b></span> <span className="zone"><b>{item.name}</b></span> <span className="should"><b>{item.sensor}</b> </span></span></Button></Link></span>
            }
            )}
          </div>
      </div>
    );
  }
}
